
//masters

export const update_masters = async ({ getters, dispatch }) => {

    const response = await fetch(`${getters.domain}/api/masters/${getters.master.id}/`, {
        method: 'PUT',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': getters.cookie,
        },
        body: JSON.stringify(getters.master)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const add_masters = async ({ getters, dispatch }) => {

    const response = await fetch(`${getters.domain}/api/masters/`, {
        method: 'POST',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': getters.cookie,
        },
        body: JSON.stringify(getters.master)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const delete_masters = async ({ getters }, id) => {

    await fetch(`${getters.domain}/api/masters/${id}/`, {
        method: 'DELETE',
        headers: {
            "Authorization": getters.cookie,
        },
    })

}

export const get_masters = async ({ commit, getters }) => {

    const response = await fetch(`${getters.domain}/api/masters/`, {
        method: 'GET',
        headers: {
            "Authorization": getters.cookie,
        },
    })

    const body = await response.json()

    commit('get_masters', body)

}

export const get_master = async ({ commit, getters }, id) => {

    const response = await fetch(`${getters.domain}/api/masters/${id}/`, {
        method: 'GET',
        headers: {
            "Authorization": getters.cookie,
        },
    })

    const body = await response.json()

    commit('get_master', body)

}