<template>
  <div class="wrap-top-slider">
      <div class="top-slider">

        <div class="top">

          <div class="city">
            <div class="back"></div>
            <div class="name">МОСКВА</div>
          </div>
          
          <div class="desc">Ремонт игровых приставок любой сложности с возможностью доставки на дом.</div>
          <div class="name"><span>TRIADA SERVICE —</span> РЕМОНТ ИГРОВОЙ ТЕХНИКИ</div>
        </div>

        <!-- slider -->
        <div class="bottom">
          <div  class="img-block">

              <div v-for="object in objects" :key="object.id" class="wrap-img-block">

                <img :src="object.poster" />

                <div class="info">
                  
                  <div class="tinsel-1"></div>

                  <div class="wrap-name">

                    <span v-for="name in object.names" :key="name.id" :class="{'wrap-spec-block': name.active}">
                      <span v-if="name.active" class="back"></span>
                      <span class="name">{{ name.name }}</span>
                    </span>

                  </div>

                  <div class="desc">{{ object.desc }}</div>

                  <div class="button-block">
                    <div class="name">Перейти</div>
                    <svg viewBox="0 0 24 9" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path d="M0 4.5H23M23 4.5L19.5342 1M23 4.5L19.5342 8" stroke="white"/>
                    </svg>
                  </div>

                </div>

              </div>

            <div class="slider-pagination">

              <div @click="prev()" class="left-arrow">
                <svg viewBox="0 0 20 17" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                 <path d="M20 8.7325L20 7.60574L2.25352 7.60574L9.01409 0.845176L8.16902 0.000105777L7.14153e-07 8.16912L8.16901 16.3381L9.01408 15.4931L2.25352 8.7325L20 8.7325Z" fill="currentColor"></path>
                </svg>
              </div>

              <div @click="next()" class="right-arrow">
                <svg viewBox="0 0 20 17" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                  <path d="M0 7.60563V8.73239H17.7465L10.9859 15.493L11.831 16.338L20 8.16901L11.831 0L10.9859 0.84507L17.7465 7.60563H0Z" fill="currentColor"></path>
                </svg>
              </div>
              
              <div class="count-masters">
                <span class="left">{{ active_slide }}</span>
                <span class="right">/{{ raw_objects.length }}</span>
              </div>

              <div class="liner">
                  <div class="back"></div>
                  <div ref="strip" class="strip" :style="{ width: width_strip + '%' }"></div>
              </div>

            </div>

          </div>

        </div>


      </div>
    </div>
</template>


<script>

import { ref, computed, onUpdated, onMounted } from "vue";


export default {


    setup(){

        const width_strip = ref(0)

        function update_width_line(){
          width_strip.value = (100 / raw_objects.value.length) * active_slide.value
        }


        onMounted(() => {
          update_width_line()
        })


        onUpdated(() => {
          update_width_line()
        })

        const raw_objects = ref([

          {
            id: 1, 
            names: [

              { id: 1, name: "Чистка и замена термопасты в", active: false, }, 
              { id: 2, name: "подарок", active: true, },

            ],

            desc: "При любом ремонте игровой прставки",
            active: true, 
            poster: "https://res.cloudinary.com/shop-consoles-ru/image/upload/v1602564260/photo_atmodn.png",
          },

          {
            id: 2, 

            names: [

              { id: 1, name: "Прозрачная", active: false, }, 
              { id: 2, name: "бесплатная", active: true, },
              { id: 3, name: "диагностика", active: false, }, 

            ],

            desc: "Бесплатная диагностика поломки, даже если вы откажитесь от ремонта", 
            active: false, 
            poster: "https://res.cloudinary.com/shop-consoles-ru/image/upload/v1630642294/ps4_z6bdnr.png",
          },

          {
            id: 3, 

            names: [
              { id: 1, name: "Открыты к сотрудничеству", active: false, }, 
            ],

            desc: "Мы принимаем на работу профессиональных специалистов", 
            active: false, 
            poster: "https://res.cloudinary.com/shop-consoles-ru/image/upload/v1630643218/Group_109_gym12h.png",
          },

          {
            id: 4, 

            names: [
              { id: 1, name: "Оригинальные", active: true, }, 
              { id: 2, name: "запчасти", active: false, }, 
            ],

            desc: "В наличии любые оригинальные запчасти на игровые приставки", 
            active: false, 
            poster: "https://res.cloudinary.com/shop-consoles-ru/image/upload/v1630642294/ps4_z6bdnr.png",
          },

        ])

        const objects = computed(() => raw_objects.value.filter((o) => o.active))

        const active_slide = computed(() => {

          let count = 0;

          for(let object of raw_objects.value){
              if(object.active) break; count++;
          }

          return count + 1

        })

        function prev(){

          let count = 0;

          for(let object of raw_objects.value){
                  
            if(object.active){
              object.active = false; break;
            }

            count++;

          }

          if(count <= 0){
            raw_objects.value[raw_objects.value.length-1].active = true;
          }else{
            raw_objects.value[count-1].active = true;
          }

          update_width_line()

        }


        function next(){
  
          let count = 0;

          for(let object of raw_objects.value){

            count++;

            if(object.active){
                object.active = false; break;
            }

          }
          
          if(count >= raw_objects.value.length){
              raw_objects.value[0].active = true;
          }else{
              raw_objects.value[count].active = true;
          }


          if(count >= raw_objects.value.length) count = 0;

          update_width_line()
          
        }


        return {

          raw_objects,
          objects,
          active_slide,
          width_strip,

          prev,
          next,

        }
    
    }

}
</script>