
//guarantees

export const update_guarantees = async ({ rootState, dispatch }, object) => {

    const response = await fetch(`${rootState.domain}/api/guarantees/${object.id}/`, {
        method: 'PUT',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': rootState.cookie,
        },
        body: JSON.stringify(object)
    })

    if(!response.ok){

        const body = await response.json()

        console.log(body)

        await dispatch('update_err_by_code', body.code)

    }

}

export const add_guarantees = async ({ rootState, dispatch}, object) => {

    const response = await fetch(`${rootState.domain}/api/guarantees/`, {
        method: 'POST',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': rootState.cookie,
        },
        body: JSON.stringify(object)
    })

    if(!response.ok){

        const body = await response.json()

        console.log(body)

        await dispatch('update_err_by_code', body.code)

    }

}

export const delete_guarantees = async ({ rootState, dispatch }, id) => {

    await fetch(`${rootState.domain}/api/guarantees/${id}/`, {
        method: 'DELETE',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    await dispatch('get_guarantees')

}

export const get_guarantees = async ({ commit, rootState }) => {

    const response = await fetch(`${rootState.domain}/api/guarantees/`, {
        method: 'GET',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    const body = await response.json()

    commit('get_guarantees', body)

}

export const get_guarantee = async ({ commit, rootState }, id) => {

    const response = await fetch(`${rootState.domain}/api/guarantees/${id}`, {
        method: 'GET',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    const body = await response.json()

    commit('get_guarantee', body)

}