

import router from '../router';


export const update_cookie = (state, cookie) => {
    state.cookie = cookie 
}



//clean new device
export const clean_treid_selected = (state) => {
    state.treid.devices.selected = [];
    state.treid.new_devices.selected = [];
}


//treid-in init
export const treid_init = async (state) => {

    const response = await fetch(`${state.domain}/api/treid-id/get`, {
        method: 'GET',
    })

    const body = await response.json()

    state.treid.devices.objects = body.user_devices
    state.treid.new_devices.objects = body.my_devices
}

//new device
export const delete_treid_new_devices_selected = (state, id) => {
    for(let i = 0; i < state.treid.new_devices.selected.length; i++){
        if(state.treid.new_devices.selected[i].id == id){
            state.treid.new_devices.selected.splice(i, 1); break
        }
    }
}

export const add_treid_new_devices_selected = (state, id) => {
    for(let device of state.treid.new_devices.objects){
        if(device.id == id){
            state.treid.new_devices.selected.push(device); break
        }
    }
}

export const update_treid_new_devices_active = (state, bool) => {
    state.treid.new_devices.active = bool
}

//user device
export const delete_treid_devices_selected = (state, id) => {
    for(let i = 0; i < state.treid.devices.selected.length; i++){
        if(state.treid.devices.selected[i].id == id){
            state.treid.devices.selected.splice(i, 1); break
        }
    }
}

export const add_treid_devices_selected = (state, id) => {
    for(let device of state.treid.devices.objects){
        if(device.id == id){
            
            state.treid.devices.selected.push({
                id: device.id, name: device.name, price: device.price, poster: device.poster, condition: {
                    active: device.condition.active,
                    objects: function(){
                        let temp = []
                        for(let condition of device.condition.objects){
                            temp.push({
                                id: condition.id, name: condition.name, active: condition.active,
                            })
                        }
                        return temp
                    }(),
                  },
            }); break
        }
    }
}

export const update_treid_devices_active = (state, bool) => {
    state.treid.devices.active = bool
}

export const update_treid_mode = (state, bool) => {
    state.treid.mode = bool
}






