export const update_menu = (state, objects) => {
    if(!state.menu.objects.length) {
        state.menu.objects = objects
    }
}

export const open_menu = state  => {
    state.menu.mobile.active = true
}

export const close_menu = state => {
    state.menu.mobile.active = false
}

export const next_menu = (state, id) => {


    //смотрим есть ли потомки у элемента
    let count = state.menu.objects.filter( o => o.parent == id ).length;

    if(count){

        state.menu.count++;

        for(let o of state.menu.objects){
            o.active = o.parent == id ? true : false;
        }

        for(let o of state.menu.objects){
            if(o.id == id){
                state.menu.parent = o.parent;
                break;
            }
        }


    }else{

        for(let o of state.menu.objects){
            if(o.id == id){

                state.menu.mobile.active = false

                router.push(`/service/${o.slug}`)
                
                break
            }
        }

    }


}

export const back_menu = state => {

    state.menu.count--;

    for(let o of state.menu.objects){
        o.active = o.parent == state.menu.parent ? true : false;
    }

    for(let o of state.menu.objects){
        if(o.id == state.menu.parent){
            state.menu.parent = o.parent; break;
        }
    }

}
