
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Добавить город</div>
            </div>

            <div class="cnt">
                
                <!--
                    <div class="name">
                        Если у вас есть вопрос по функционалу симулятора или вы предполагаете, что что-то работает не так, как должно, напишите вопрос в форме. Мы можем связаться с вами и помочь разобраться и обязательно будем работать над улучшением игры.
                    </div>
                -->

                <div class="wrap input">
                    <label for="input-180">Наименование</label>
                    <input v-model="city.name" id="input-180" />
                </div>
                
                <div class="wrap input">
                    <label for="input-180">Короткий адрес</label>
                    <input v-model="city.slug" id="input-180" />
                </div>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="city.default" id="input-180" />
                    <label for="input-180">Дефолтный город?</label>
                </div>

                <div class="wrap input">
                    <label for="input-180">Альтернативное наименование?</label>
                    <input v-model="city.alt_name" id="input-180" />
                </div>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="city.is_metro" id="input-180" />
                    <label for="input-180">Есть ли метро?</label>
                </div>


                <!--
                    <div class="desc">Вы можете указать контакт для связи, если хотите, чтобы мы связались с вами для обсуждения.</div>
                -->

            </div>

            <div class="btm">
                <div @click="add()" :class="['send', {active: true}]">Добавить</div>
                <router-link :to="{ name: `admin.citys`, }" class="cancel">Отмена</router-link>
            </div>


        </div>

    </div>

</template>

<script>

import { computed } from "vue"
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default {

	setup(){

        const router    = useRouter()
        const store     = useStore()
        const city      = computed(() => store.getters.city)

        async function add(){
            
            await store.dispatch('add_citys')

            router.push({ name: `admin.citys` })
            
        }

        return {
            add,
            city,
        }
    
	}
}

</script>
