<script>

import { ref, onMounted, computed } from "vue";
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

import Content from '@/components/service/Content.vue';
import Order from '@/components/service/Order.vue';

import { convert_image } from "@/api/func" 

export default {

	components: { 
		"my-content": Content,
		"order": Order,
	},

	setup(){

		const route = useRoute()
		const store = useStore()
		const domain = computed(() => store.getters.domain)

		const object = ref({})
		const question = ref(false)


		//апдейт сервиса
		async function update() {

			const response = await fetch(`${domain.value}/api/service/${route.params.slug}`, {
				method: 'GET',
			})

			const body = await response.json()

			object.value = body

		}

		onMounted(async () => { 
			await update()
			document.title = `${object.value.name}`
		})

		return {
			domain,
			object,
			question,
			convert_image,
		}

	}

}

</script>