
import { set_cookie } from "@/api/cookie";


export const clear_user = ({ commit }) => {
    commit('clear_user')
}

export const update_cookie = ({ commit }, cookie) => {
    commit('update_cookie', cookie)
}

export const get_user = async ({ commit, getters }) => {

    if(getters.cookie){

        let response = await fetch(`${getters.domain}/auth/users/me/`, {
            method: 'GET',
            headers: {
                "Authorization": getters.cookie,
            },
        })

        if(response.ok){
        
            let body = await response.json()

            commit('get_user', body); return
        }

        //чистим куки в браузере
        set_cookie("token", "", 0)

    }

    commit('get_user', false)
 
}