</template>

<script>

import { computed } from 'vue'
import { useStore } from 'vuex'

import MobileMenu from "./header/MobileMenu.vue" 

export default {

  components: { 
    'mobile-menu': MobileMenu 
  },

  setup(){

    const store   = useStore()
    const domain  = computed(() => store.getters.domain)
    const menu    = computed(() => store.getters.menu)

    async function build () {

      let response = await fetch(`${domain.value}:3000`, {
        method: 'GET',
      })
    
      response = await fetch(`${domain.value}/api/dev/build/static`, {
        method: 'GET',
      })

      console.log(response)
    }


    function open(){
      store.dispatch('open_menu')
    }

    function close(){
      store.dispatch('close_menu')
    }

    return { domain, menu, open, close, build }
    
  },

}

</script>

<style>
.build {
    color: black;
    cursor: pointer;
}
</style>