
//services

export const update_services = async ({ rootState, dispatch }, object) => {

    const response = await fetch(`${rootState.domain}/api/services/${object.id}/`, {
        method: 'PUT',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': rootState.cookie,
        },
        body: JSON.stringify(object)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const add_services = async ({ rootState, dispatch}, object) => {

    const response = await fetch(`${rootState.domain}/api/services/`, {
        method: 'POST',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': rootState.cookie,
        },
        body: JSON.stringify(object)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const delete_services = async ({ rootState, dispatch }, id) => {

    await fetch(`${rootState.domain}/api/services/${id}/`, {
        method: 'DELETE',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    await dispatch('get_services')

}

export const get_services = async ({ commit, rootState }) => {

    const response = await fetch(`${rootState.domain}/api/services/`, {
        method: 'GET',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    const body = await response.json()

    commit('get_services', body)

}

