<template>

    <div class="slider">
        
        <div v-touch:swipe.left="next" v-touch:swipe.right="prev" class="image">

            <picture v-for="o in active" :key="o.id">
                <source :srcset="domain + o.name + '_750.webp, ' + domain + o.name + '_1500.webp 2x'" type="image/webp">
                <img :src="domain + o.name + '_750.webp'" :srcset="domain + o.name + '_750.webp, ' + domain + o.name + '_1500.webp 2x'">
            </picture>

        </div>
        
        <bonus :bonus="object.bonus"></bonus>

        <div class="slider-count">{{ active_slide }}/{{ object.images.length }}</div>

        <div v-if="object.images.length > 1" class="slider-pagination">

            <div @click="prev" class="arr-left">
                <svg width="45" height="45" viewBox="0 0 45 45" fill="none" xmlns="http://www.w3.org/2000/svg"><circle cx="22.5" cy="22.5" r="22.5" fill="#FAFAFA"></circle><rect width="12.9479" height="2.09214" rx="1" transform="matrix(-0.725954 0.687743 -0.664328 -0.747442 27 15.0645)" fill="black"></rect><rect width="12.9624" height="2.09214" rx="1" transform="matrix(-0.725954 -0.687743 0.664328 -0.747442 25.6074 31.5)" fill="black"></rect></svg>
            </div>

            <picture>
                <source :srcset="domain + one + '_750.webp, ' + domain + one + '_1500.webp 2x'" type="image/webp">
                <img @click="prev"  :src="domain + one + '_750.webp'" :srcset="domain + one + '_750.webp, ' + domain + one + '_1500.webp 2x'" class="pagination-img">
            </picture>

            <picture v-for="o in active" :key="o.id">
                <source :srcset="domain + o.name + '_750.webp, ' + domain + o.name + '_1500.webp 2x'" type="image/webp">
                <img :src="domain + o.name + '_750.webp'" :srcset="domain + o.name + '_750.webp, ' + domain + o.name + '_1500.webp 2x'" class="pagination-active">
            </picture>

            <picture>
                <source :srcset="domain + three + '_750.webp, ' + domain + three + '_1500.webp 2x'" type="image/webp">
                <img @click="next"  :src="domain + three + '_750.webp'" :srcset="domain + three + '_750.webp, ' + domain + three + '_1500.webp 2x'" class="pagination-img">
            </picture>

            <div @click="next" class="arr-right">
                <svg width="45" height="45" viewBox="0 0 45 45" fill="none" xmlns="http://www.w3.org/2000/svg"><circle cx="22.5" cy="22.5" r="22.5" fill="#FAFAFA"></circle><rect width="12.9479" height="2.09214" rx="1" transform="matrix(0.725954 -0.687743 0.664328 0.747442 18 29.9355)" fill="black"></rect><rect width="12.9624" height="2.09214" rx="1" transform="matrix(0.725954 0.687743 -0.664328 0.747442 19.3926 13.5)" fill="black"></rect></svg>
            </div>

        </div>

    </div>

</template>

<script>

import { ref, computed } from "vue";
import { useStore } from 'vuex'

import Bonus from '@/components/service/detail/Bonus.vue';

import { convert_image } from "@/api/func" 

export default {

    components: { 
		"bonus": Bonus,
	},

    setup(){

        const store     = useStore()
        const domain    = computed(() => store.getters.domain)
        const object    = computed(() => store.getters.sub_service)

        const question = ref(false)

        const active = computed(() => object.value.images.filter((o) => o.active))

        const one = computed(() => {

			let count = 0;

            for(let image of object.value.images){
                if(image.active) break; count++;
            }

            count -= 1;

            if(count < 0){
                return object.value.images[object.value.images.length-1].name; 
            }else{
                return object.value.images[count].name;
            }

		})

        const three = computed(() => {

			let count = 0;

            for(let image of object.value.images){
                if(image.active) break; count++;
            }

            count += 1;

            if(count >= object.value.images.length){
                return object.value.images[0].name; 
            }else{
                return object.value.images[count].name;
            }

		})

        const active_slide = computed(() => {

			let count = 0;

            for(let image of object.value.images){
                if(image.active) break; count++;
            }

            return count + 1

		})

        function next(){

            let count = 0;

            for(let image of object.value.images){

                count++;

                if(image.active){
                    image.active = false; break;
                }

            }

            if(count >= object.value.images.length){
                object.value.images[0].active = true;
            }else{
                object.value.images[count].active = true;
            }

        }

        function prev(){

            let count = 0;

            for(let image of object.value.images){
                
                if(image.active){
                    image.active = false; break;
                }

                count++;

            }

            if(count <= 0){
                object.value.images[object.value.images.length-1].active = true;
            }else{
                object.value.images[count-1].active = true;
            }

        }

        return {
            domain,
            convert_image,
            object,
            question,
            active,
            one,
            three,
            active_slide,
            next,
            prev,
        }

    }


}

</script>