

export const update_images = async ({ getters, dispatch }, object) => {

    const response = await fetch(`${getters.domain}/api/services/0/images/${object.id}/`, {
        method: 'PUT',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': getters.cookie,
        },
        body: JSON.stringify(object)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const add_images = async ({ getters, dispatch }) => {

    const response = await fetch(`${getters.domain}/api/services/0/images/`, {
        method: 'POST',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': getters.cookie,
        },
        body: JSON.stringify(getters.image)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const delete_images = async ({ getters }, id) => {

    await fetch(`${getters.domain}/api/services/0/images/${id}/`, {
        method: 'DELETE',
        headers: {
            "Authorization": getters.cookie,
        },
    })

}

export const get_images = async ({ commit, getters }, id) => {

    const response = await fetch(`${getters.domain}/api/services/${id}/images/`, {
        method: 'GET',
        headers: {
            "Authorization": getters.cookie,
        },
    })

    const body = await response.json()

    commit('get_images', body)

}

