
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Добавление Доставки</div>
            </div>

            <div class="cnt">
                
                <!--
                    <div class="name">
                        Если у вас есть вопрос по функционалу симулятора или вы предполагаете, что что-то работает не так, как должно, напишите вопрос в форме. Мы можем связаться с вами и помочь разобраться и обязательно будем работать над улучшением игры.
                    </div>
                -->

                <div class="wrap input">
                    <label for="input-180">Наименование</label>
                    <input v-model="delivery.name" id="input-180" />
                </div>
                
                <div class="wrap input">
                    <label for="input-180">Цена</label>
                    <input v-model="delivery.price" id="input-180" />
                </div>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="delivery.default" id="input-180" />
                    <label for="input-180">Дефолтная доставка?</label>
                </div>

                <!--
                    <div class="desc">Вы можете указать контакт для связи, если хотите, чтобы мы связались с вами для обсуждения.</div>
                -->

            </div>

            <div class="btm">
                <div @click="add()" :class="['send', {active: true}]">Добавить</div>
                <router-link :to="{ name: `admin.deliverys`, }" class="cancel">Отмена</router-link>
            </div>


        </div>

    </div>
</template>

<script>


import { computed } from "vue"
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'


export default {
	setup(){

        const router    = useRouter()
        const store     = useStore()
        const delivery  = computed(() => store.getters.delivery)

        async function add(){
            
            await store.dispatch('add_deliverys', delivery.value)

            router.push({ name: `admin.deliverys` })
            
        }

        return { delivery, add }

	}
}

</script>