
//citys

export const update_citys = async ({ rootState, dispatch }, object) => {

    const response = await fetch(`${rootState.domain}/api/citys/${object.id}/`, {
        method: 'PUT',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': rootState.cookie,
        },
        body: JSON.stringify(object)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const add_citys = async ({ rootState, dispatch, state}) => {

    const response = await fetch(`${rootState.domain}/api/citys/`, {
        method: 'POST',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': rootState.cookie,
        },
        body: JSON.stringify(state.city)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const delete_citys = async ({ rootState }, id) => {

    await fetch(`${rootState.domain}/api/citys/${id}/`, {
        method: 'DELETE',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

}

export const get_citys = async ({ commit, rootState }) => {

    const response = await fetch(`${rootState.domain}/api/citys/`, {
        method: 'GET',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    const body = await response.json()

    commit('get_citys', body)

}

export const get_city = async ({ commit, rootState }, id) => {

    const response = await fetch(`${rootState.domain}/api/citys/${id}/`, {
        method: 'GET',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    const body = await response.json()

    commit('get_city', body)

}