
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Добавить блок контента</div>
            </div>

            <div class="cnt">
                
                <!--
                    <div class="name">
                        Если у вас есть вопрос по функционалу симулятора или вы предполагаете, что что-то работает не так, как должно, напишите вопрос в форме. Мы можем связаться с вами и помочь разобраться и обязательно будем работать над улучшением игры.
                    </div>
                -->

                <form-image></form-image>

                <div class="wrap textarea">
                    <label for="input-179">Контент</label>
                    <textarea id="input-179" v-model="content.content" placeholder="Опишите блок" rows="5"></textarea>
                </div>

                <!--
                    <div class="desc">Вы можете указать контакт для связи, если хотите, чтобы мы связались с вами для обсуждения.</div>
                -->

                <div class="wrap input">
                    <label for="input-180">Сортировка</label>
                    <input v-model="content.order" id="input-180" placeholder="Как будут сортироваться блоки" />
                </div>

            </div>

            <div class="btm">
                <div @click="create()" :class="['send', {active: content.content.length > 0}]">Добавить</div>
            </div>


        </div>

    </div>

</template>

<script>

import { computed, onUnmounted } from "vue"
import { useStore } from 'vuex'
import { useRoute, useRouter } from 'vue-router'

import Image from '@/components/form/Image.vue';


export default {

    components: { 
        "form-image": Image,
    },

	setup(){

        const router    = useRouter()
        const route     = useRoute()
        const store     = useStore()
        const url       = computed(() => store.getters.url)
        const content   = computed(() => store.getters.content)

        onUnmounted(async () => {
            if(url.value != ''){
                await store.dispatch('delete_image_server', url.value)
            }
        })


        async function create(){

            content.value.service = route.params.id

            content.value.image = url

            await store.dispatch('add_contents')

            await store.dispatch('update_url', '')

            router.push({ name: `admin.services.contents`, params: { id: route.params.id } })

        }

        return {
            create,
            content,
        }
    
	}
}

</script>
