
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Добавить фотку</div>
            </div>

            <div class="cnt">

                <form-image></form-image>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="image.default" id="input-180" />
                    <label for="input-180">Дефолтная фотка?</label>
                </div>

            </div>

            <div class="btm">
                <div @click="create()" :class="['send', 'active']">Добавить</div>
                <div @click="back()" class="cancel">Отмена</div>
            </div>


        </div>

    </div>

</template>

<script>

import { computed } from "vue"
import { useStore } from 'vuex'
import { useRoute, useRouter } from 'vue-router'

import Image from '@/components/form/Image.vue';


export default {

    components: { 
        "form-image": Image,
    },

	setup(){

        const router    = useRouter()
        const route     = useRoute()
        const store     = useStore()
        const url       = computed(() => store.getters.url)
        const image 	= computed(() => store.getters.image)

        async function back(){

            if(url.value != ''){
                await store.dispatch('delete_image_server', url.value)
            }

            router.push({ name: `admin.images` })

        }

        async function create(){

            image.value.parent = route.params.sd_id

            image.value.image = url

            await store.dispatch('add_images')

            await store.dispatch('update_url', '')

            router.push({ name: `admin.images` })

        }

        return {
            create,
            image,
            back,
        }
    
	}
}

</script>
