<template>
 
    <div class="form">

        <div class="right">

            <img class="mishyra" src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1629348416/Polygon_4_bnauhu.png"/>
            <img class="mishyra-2" src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1629348410/Polygon_3_kpsgyp.png"/>
            <img class="mishyra-3" src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1629348416/Polygon_4_bnauhu.png"/>

            <div class="wrap">

                <div class="wrap">
                    <div class="name">Модель устройства<span>*</span></div>

                    <div @click="update_devices_active(!devices.active)" class="select">
                        <div class="name">Выберите своё устройство</div>

                        <svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1 1L8 7L15 1" stroke="white"/>
                        </svg>
                    </div>

                    <div v-if="devices.active" class="wrap">
                        <div v-for="device in devices.objects" :key="device.id" @click="add_treid_devices_selected(device.id)" class="name">{{ device.name }}</div>
                    </div>

                </div>

                <div v-if="!mode" class="wrap">
                    <div class="name">Модель нового устройства<span>*</span></div>

                    <div @click="update_new_devices_active(!new_devices.active)" class="select">
                        <div class="name">Выберите новое устройство</div>

                        <svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1 1L8 7L15 1" stroke="white"/>
                        </svg>
                    </div>

                    <div v-if="new_devices.active" class="wrap">
                        <div v-for="device in new_devices.objects" :key="device.id" @click="add_treid_new_devices_selected(device.id)" class="name">{{ device.name }}</div>
                    </div>

                </div>


                <div class="wrap">
                    <div class="name">ИМЯ<span>*</span></div>

                    <div class="input">
                        <input v-model="name" type="text" placeholder="Как вас зовут?"/>
                    </div>

                </div>

                <div class="wrap">
                    <div class="name">Телефон<span>*</span></div>

                    <div class="input">
                        <input v-model="phone" type="text" placeholder="Ваш номер телефона"/>
                    </div>

                </div>

                <div class="wrap">
                    <div class="name">Дополнительная информация</div>

                    <div class="input">
                        <input v-model="comment" type="text" placeholder="Дополнительная информация"/>
                    </div>
                </div>

            </div>

            <div class="conf">
                <div class="wrap"></div>
                <div class="name">
                    Я соглашаюсь с политикой конфиденциальности
                </div>
            </div>

            <div class="desc">
                Поля, отмеченные специальным символом * обязательны для заполнения заявки
            </div>

            <div @click="order()" class="button">Отправить заявку</div>

        </div>

    </div>

</template>

<script>

import { useStore } from 'vuex'
import { computed, ref } from "vue";

export default {

    setup(){

        const store         = useStore()
        const domain        = computed(() => store.getters.domain)
        const mode          = computed(() => store.getters.mode)
        const devices       = computed(() => store.getters.devices)
        const new_devices   = computed(() => store.getters.new_devices)


        const name = ref('')
		const phone = ref('')
        const comment = ref('')



        async function order(){

            const data = JSON.stringify({
                
                user_name: name.value, 
                user_phone: phone.value, 
                comment: comment.value, 

                mode: mode.value,
                user_device: devices.value.selected.map(item => {
                    return {
                        name: item.name, 
                        price: item.price, 
                        condition: item.condition.objects.filter((o) => o.active)[0].name
                    }
                }),
                new_devices: new_devices.value.selected.map(item => {
                    return {name: item.name, price: item.price,}
                }),


            })

            const response = await fetch(`${domain.value}/api/traid-in/order`, {
                method: 'POST',
                headers: { 
                    'Content-Type':'application/x-www-form-urlencoded',
                },
                body: `payload=${data}`
            })

            const body = await response.json()

            if(body.err.is){
                store.dispatch('set_err', body.err); return
            }

            store.dispatch("clean_treid_selected")
            name.value = ""
            phone.value = ""
            comment.value = ""
        }







        //new device
        function update_new_devices_active(bool){
            store.dispatch("update_treid_new_devices_active", bool)
        }

        function add_treid_new_devices_selected(id){
            store.dispatch("add_treid_new_devices_selected", id); update_new_devices_active(false)
        }


        //user device
        function update_devices_active(bool){
            store.dispatch("update_treid_devices_active", bool)
        }

        function add_treid_devices_selected(id){
            store.dispatch("add_treid_devices_selected", id); update_devices_active(false)
        }








        return {

            mode,
            order,

            devices,
            update_devices_active,
            add_treid_devices_selected,


            new_devices,
            update_new_devices_active,
            add_treid_new_devices_selected,


            name,
			phone,
            comment,

        }
    }
}

</script>