
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Основная информация</div>
            </div>

            <div class="cnt">
                
                <!--
                    <div class="name">
                        Если у вас есть вопрос по функционалу симулятора или вы предполагаете, что что-то работает не так, как должно, напишите вопрос в форме. Мы можем связаться с вами и помочь разобраться и обязательно будем работать над улучшением игры.
                    </div>
                -->

                <div class="wrap input">
                    <label for="input-180">Наименование</label>
                    <input v-model="service.name" id="input-180" />
                </div>

                <div class="wrap textarea">
                    <label for="input-180">Описание</label>
                    <textarea v-model.trim="service.pre_content"></textarea>
                </div>
                
                
                <div class="wrap input">
                    <label for="input-180">Короткий адрес</label>
                    <input v-model="service.slug" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">Сео наименование</label>
                    <input v-model="service.seo_t" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">Сео описание</label>
                    <input v-model="service.seo_d" id="input-180" />
                </div>

                <!--
                    <div class="desc">Вы можете указать контакт для связи, если хотите, чтобы мы связались с вами для обсуждения.</div>
                -->

            </div>

            <div class="btm">
                <div @click="update()" :class="['send', {active: true}]">изменить</div>
                <!-- <router-link :to="{ name: `admin.services.info`, }" class="cancel">Отмена</router-link> -->
            </div>


        </div>

    </div>

</template>

<script>

import { computed, onMounted } from "vue"
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

export default {
	setup(){

        const route     = useRoute()

		const store     = useStore()
        const service 	= computed(() => store.getters.service)

        async function update() {
			await store.dispatch('update_services', service.value)
		}

        onMounted(async () => await store.dispatch('get_service', route.params.id))


		return { service, update }
	}
}

</script>