<template>

    <div class="order">

        <div class="left"></div>
        <div class="right"></div>

        <div class="wrap">

            <div class="calc">
                <div class="wrap">

                    <div class="name">
                        Калькулятор
                    </div>

                    <div class="mode">

                        <div @click="select_mode(true)" class="wrap">
                            <div class="back"></div>
                            <div :class="['name', {active: mode}]">Скупка</div>
                            <img src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1633170829/photo_zne4ii.png" />
                        </div>

                        <div @click="select_mode(false)" class="wrap">
                            <div class="back"></div>
                            <div :class="['name', {active: !mode}]">Trade-in</div>
                            <img src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1633170830/pss_cjbbqx.png" />
                        </div>

                    </div>

                    <my-form></my-form>
                    
                </div>
            </div>

            <result></result>

        </div>

    </div>

</template>

<script>

import { useStore } from 'vuex'
import { computed, onMounted } from "vue";


import Form from '@/components/traidin/orders/Form.vue';
import Result from '@/components/traidin/orders/Result.vue';

export default {

    components: { 
		"my-form": Form,
        "result": Result,
    },

    setup(){

        const store = useStore()
        const mode = computed(() => store.getters.mode)


        function select_mode(bool){
            store.dispatch("update_treid_mode", bool)
        }

        onMounted(() => store.dispatch("treid_init"))


        return {
            mode,
            select_mode,
        }
    }
}

</script>