<script>

import { ref, computed, onUpdated } from "vue";
import { trun } from "@/api/func" 

export default {

    setup(){

        /* slider 2 */

        const objects = ref([

            {
                id: 1, 
                default: true,
                name: "Заказал геймпад на ps4 dualshock 2. Цена за оригинальный контроллер оказалась приемлемой, качество тоже. О доставке договорились быстро, курьер приехал буквально через пару часов. Спасибо большое!!!",
            },

            {
                id: 2,
                default: false,
                name: "Была проблема с прошивкой на ps vita slim,позвонил договорислся,приехал все сделали четко,грамотно прошивка супер ничего не виснет,ошибок не выдает,все грамотно четко,сразу видно что люди проффесионалы и денег за свой труд много не берут.вежливые,б/у хлам не продают,только новье,и делают на совесть да еще сказали что если будет",
            },

            {
                id: 3,
                default: false,
                name: "Спасибо за Nintendo Switch! Играть теперь не переиграть во все игры. Официальные дилеры Xecuter SX PRO. качественный продукт, хорошая поддержка и консультации. Очень классная и незаменимая штука. Рекомендую Триада-Сервис. Знаю куда теперь обратиться, при необходимости. ",
            },

            {
                id: 4,
                default: false,
                name: "Заказал геймпад на ps4 dualshock 2. Цена за оригинальный контроллер оказалась приемлемой, качество тоже. О доставке договорились быстро, курьер приехал буквально через пару часов. Спасибо большое!!!",
            },  

        ])

        const back          = ref(null)
        const left_position = ref(0)
        const back_width    = ref(0)
        const width_strip   = ref(0)

        onUpdated(() => {
          back_width.value    = back.value.clientWidth;
          width_strip.value   = back_width.value / objects.value.length;
        })

        const current_position = computed(() => {
          let count = 0;

          for(let object of  objects.value){
            if(object.default) break; count++;
          }
          
          return count + 1

        })
		
        async function next(){

          const temp = objects.value[0]
          objects.value.splice(0, 1)
          objects.value.push(temp)

        }

        async function prev(){
        
          const last = objects.value[objects.value.length-1]
          objects.value.splice(objects.value.length-1, 1)
          objects.value.unshift(last)

        }

        return {

            objects,

            back,
            left_position,
            back_width,
            width_strip,

            current_position,

            prev,
            next,

            trun,
        }
    
    }

}
</script>

