
<template>

	<router-link :to="{ name: `admin.images.add` }" class="name">Добавить изображение</router-link>

	<div class="objects">

		<div v-for="object in images" :key="object.id" class="object">
			<img :src="domain + object.image + '_750.webp'" />
			<router-link :to="{ name: `admin.images.detail`, params: {i_id: object.id} }" class="name">Изображение {{ object.default ? ' - Дефолтная' : '' }}</router-link>
			<svg @click="del(object)" aria-hidden="true" focusable="false" data-prefix="fal" data-icon="times" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><path fill="currentColor" d="M193.94 256L296.5 153.44l21.15-21.15c3.12-3.12 3.12-8.19 0-11.31l-22.63-22.63c-3.12-3.12-8.19-3.12-11.31 0L160 222.06 36.29 98.34c-3.12-3.12-8.19-3.12-11.31 0L2.34 120.97c-3.12 3.12-3.12 8.19 0 11.31L126.06 256 2.34 379.71c-3.12 3.12-3.12 8.19 0 11.31l22.63 22.63c3.12 3.12 8.19 3.12 11.31 0L160 289.94 262.56 392.5l21.15 21.15c3.12 3.12 8.19 3.12 11.31 0l22.63-22.63c3.12-3.12 3.12-8.19 0-11.31L193.94 256z"></path></svg>
		</div>

	</div>

</template>

<script>

import { onMounted, computed } from "vue"
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

export default {
	setup(){

		const route 	= useRoute()
		const store 	= useStore()
		const domain 	= computed(() => store.getters.domain)
		const images 	= computed(() => store.getters.images)

		onMounted(async () => await store.dispatch('get_images', route.params.sd_id))

		async function del(object) {

			await store.dispatch('delete_image_server', object.image)

			await store.dispatch('delete_images', object.id)

			await store.dispatch('get_images', route.params.sd_id)

		}

		return { domain, images, del }
	}
}

</script>

