<script>

import { ref, onUpdated, computed } from "vue";
import { useStore } from 'vuex'

export default {

    setup(){

        const store         = useStore()
        const domain        = computed(() => store.getters.domain)
        const active_city   = computed(() => store.getters.active_city)
        const object        = computed(() => store.getters.sub_service)


        const back          = ref(null)
		const question      = ref(false)

		const left_position = ref(0)
		const back_width    = ref(0)
		const width_strip   = ref(0)

        onUpdated(() => {
			back_width.value    = back.value.clientWidth;
			width_strip.value   = back_width.value / object.value.masters.length;
		})



        const current_position = computed(() => {
			let count = 0;

			for(let m of object.value.masters){
				if(m.default) break; count++;
			}
			
			return count + 1

		})

		const active_master = computed(() => object.value.masters.filter((o) => o.default))

		const next_master = computed(() => {

			let count = 0;

			for(let master of object.value.masters){

                count++;

                if(master.default) break;

            }

			if(count >= object.value.masters.length){
				count = 0;
			}

			return [object.value.masters[count]]

		})

		function set_master(master){

            for(let m of object.value.masters){
				m.default = false;
			}
			
			master.default = true;

			let count = 0;

            for(let m of object.value.masters){
				if(m.default) break; count++;
			}

			left_position.value = (back_width.value / object.value.masters.length) * count;

        }

		function prev(){

            let count = 0;

            for(let m of object.value.masters){
                
                if(m.default){
                    m.default = false; break;
                }

                count++;

            }

            if(count <= 0){
                object.value.masters[object.value.masters.length-1].default = true;
            }else{
                object.value.masters[count-1].default = true;
			}
			
			if(count <= 0){
				count = object.value.masters.length;
			}

			count--;

			left_position.value = (back_width.value / object.value.masters.length) * count;

		}

		function next(){
			
			let count = 0;

			for(let master of object.value.masters){

                count++;

				if(master.default){
                    master.default = false; break;
                }

			}
			
			if(count >= object.value.masters.length){
                object.value.masters[0].default = true;
            }else{
                object.value.masters[count].default = true;
            }


			if(count >= object.value.masters.length){
				count = 0;
			}

			left_position.value = (back_width.value / object.value.masters.length) * count;
			
		}



        return {
            domain,
            active_city,

			back,
			object,
			question,
			left_position,
			back_width,
			width_strip,

			current_position,
			active_master,
			next_master,

			set_master,
			prev,
			next,

        }
    }
}

</script>