<template>
	<div class="main service detail">
		<div class="wrap-detail-service">

			<!-- head -->
			<head-block></head-block>

			<!-- content -->
			<my-content :objects="object.contents"></my-content>

			<!-- similar-service -->
			<simular-service></simular-service>

			<!-- masters -->
			<masters v-if="object.masters.length > 0"></masters>

			<!-- order -->
			<service-order></service-order>

			<!-- comments -->
			<!-- <comments></comments> -->

		</div>
	</div>
</template>

<script>

import { watch, onMounted, computed } from "vue";
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'


import Content from '@/components/service/Content.vue';
//import Comments from '@/components/service/Comments.vue';
import HeadBlock from '@/components/service/detail/HeadBlock.vue';
import SimularService from '@/components/service/detail/SimularService.vue';
import Masters from '@/components/service/detail/Masters.vue';
import OrderBlock from '@/components/service/detail/OrderBlock.vue';


export default {

	components: { 
		"head-block": HeadBlock, 
		"my-content": Content,
		"simular-service": SimularService,
		"masters": Masters,
		"service-order": OrderBlock,
		//"comments": Comments,
	},

	setup(){

		const route 		= useRoute()
		const store 		= useStore()
		const domain 		= computed(() => store.getters.domain)
		const active_city 	= computed(() => store.getters.active_city)
		const object 		= computed(() => store.getters.sub_service)

		
		async function update() {

			const response = await fetch(`${domain.value}/api/service/detail/${route.params.slug}`, {
				method: 'GET',
			})

			const body = await response.json()

			store.dispatch('set_sub_service', body)

		}

		onMounted(async () => { 
			await update()
			document.title = `${object.value.name}`
		})

		watch(active_city, async () => { 
			await update()
			document.title = `${object.value.name}`
		})

		return {
			object,
		}


	}

}

</script>