                        </svg>
                    </div>

                    <div class="name-block">
                        <div class="name">
                            Мастерская:
                        </div>
                        <div class="desc">
                            {{ master.name }}
                        </div>
                    </div>

                </div>

                <div class="result-price-block">

                    <img src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1601184859/Polygon_4_txgyfz.png" class="icon-absolute-left" />

                    <div class="name">Услуга:</div>
                    <div class="price-block">
                        <div class="name">{{ object.name }}</div>
                        <div class="price">{{ object.price }}₽</div>
                    </div>

                </div>

                <div class="result-price-block">
                    <div class="name">Гарантия:</div>
                    <div v-for="guarantee in object.guarantee" :key="guarantee.id" v-show="guarantee.default" class="price-block">
                        <div class="name">{{ guarantee.name }}</div>
                        <div class="price">{{ guarantee.price }}₽</div>
                    </div>
                </div>

                <div v-if="get_active_simular_service.length" class="result-price-block">
                    <div class="name">Дополнительный сервис:</div>
                    <div v-for="simular_service in get_active_simular_service" :key="simular_service.id" class="price-block">
                        <div class="name">{{ simular_service.name }}</div>
                        <div class="price">{{ simular_service.price }}₽</div>
                    </div>
                </div>

                <div v-if="get_active_spare_parts.length" class="result-price-block">
                    <div class="name">Запчасти:</div>
                    <div v-for="spare_part in get_active_spare_parts" :key="spare_part.id" class="price-block">
                        <div class="name">{{ spare_part.name }}</div>
                        <div class="price">{{ spare_part.price }}₽</div>
                    </div>
                </div>

                <div class="result-price-block">

                    <img src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1601185035/Polygon_5_yniz4x.png" class="icon-absolute-right" />

                    <div class="name">Вид транспортировки:</div>
                    
                    <div class="price-block">
                        <div v-for="delivery in active_delivery" :key="delivery.id" class="name">{{ delivery.name }} - {{ delivery.price }} руб.</div>
                    </div>

                </div>

                <div class="result-price-count">Итоговая цена: {{ result_order_price }} ₽</div>

            </div>

        </div>

    </div>
</template>

<script>

import { computed } from "vue";
import { useStore } from 'vuex'

export default {

    setup(){

        const store                         = useStore()
        const object                        = computed(() => store.getters.sub_service)
        const active_master                 = computed(() => object.value.masters.filter((o) => o.default))
        
        const active_delivery               = computed(() => object.value.delivery.filter((o) => o.active))
		const get_active_simular_service    = computed(() => object.value.simular_service.filter((o) => o.active))
		const get_active_spare_parts        = computed(() => object.value.spare_parts.filter((o) => o.active))

        const result_order_price = computed(() => {

			let count = object.value.price;

			for(let g of object.value.guarantee){
				if(g.default){
					count += g.price;
				} 
			}
			
			for(let simular_service of get_active_simular_service.value){
				if(simular_service.active){
					count += simular_service.price;
				}
			}

			for(let spare_parts of get_active_spare_parts.value){
				if(spare_parts.active){
					count += spare_parts.price;
				}
			}

			for(let delivery of active_delivery.value){
				if(delivery.active){
					count += delivery.price;
				}
			}

			return count;

		})

        return {
            object,
            active_master,
            active_delivery,
            get_active_simular_service,
            get_active_spare_parts,
            result_order_price,
        }
    }
}

</script>