
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Добавить мастера</div>
            </div>

            <div class="cnt">
                
                <!--
                    <div class="name">
                        Если у вас есть вопрос по функционалу симулятора или вы предполагаете, что что-то работает не так, как должно, напишите вопрос в форме. Мы можем связаться с вами и помочь разобраться и обязательно будем работать над улучшением игры.
                    </div>
                -->

                <a v-if="master.metro_icon" :href="`${domain + master.metro_icon}_1500.webp`" target="_blank" class="img">
                    <img :src="`${domain + master.metro_icon}_750.webp`" />
                </a>

                <form-image></form-image>

                <div class="wrap input">
                    <label for="input-180">Наименование метро</label>
                    <input v-model="master.metro_name" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">Наименование</label>
                    <input v-model="master.name" id="input-180" />
                </div>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="master.default" id="input-180" />
                    <label for="input-180">Дефолтная мастерская?</label>
                </div>

                <!--
                    <div class="desc">Вы можете указать контакт для связи, если хотите, чтобы мы связались с вами для обсуждения.</div>
                -->

            </div>

            <div class="btm">
                <div @click="add()" :class="['send', {active: true}]">Добавить</div>
                <div @click="back()" class="cancel">Отмена</div>
            </div>


        </div>

    </div>

</template>

<script>

import { computed } from "vue"
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

import Image from '@/components/form/Image.vue';



export default {

    components: { 
        "form-image": Image,
    },

	setup(){

        const router    = useRouter()
        const store     = useStore()
        const domain    = computed(() => store.getters.domain)
        const url       = computed(() => store.getters.url)
        const master    = computed(() => store.getters.master)

        async function back(){

            if(url.value != ''){
                await store.dispatch('delete_image_server', url.value)
            }

            router.push({ name: `admin.masters` })

        }


        async function add(){

            if(url.value != ''){
                
                await store.dispatch('delete_image_server', master.value.metro_icon)
            
                master.value.metro_icon = url
            }

            await store.dispatch('add_masters')

            await store.dispatch('update_url', '')

            router.push({ name: `admin.masters` })
            
        }

        return {
            domain,
            add,
            master,
            back,
        }
    
	}
}

</script>
