<template>

    <div class="result">

        <div class="wrap">
            <div class="name">{{ mode ? 'Стоимость вашей техники' : 'Ваша выгода' }}</div>
            <div class="desc">{{ get_devices_final_price() }} ₽</div>
        </div>

        <div v-if="!mode" class="wrap">
            <div class="name">Стоимость новой техники</div>
            <div class="desc">{{ get_new_devices_final_price() - get_devices_final_price() }} ₽ <span>*</span></div>
            <div class="name-2">{{ get_new_devices_final_price() }}₽</div>
        </div>

        <div v-if="devices.selected.length > 0" class="name">
            Ваша техника
        </div>

        <div v-for="device in devices.selected" :key="device.id" class="device">

            <div class="line"></div>

            <img :src="device.poster" />

            <div class="wrap">

                <div class="name">
                    {{ device.name }}
                </div>

                <div class="condition">

                    <div class="name">
                        Состояние:
                    </div>

                    <div @click="device.condition.active = !device.condition.active" class="wrap">
                        <div class="name">{{ get_condition_name(device.condition.objects) }}</div>
                        <svg viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 1L8 7L15 1" stroke="white"/></svg>
                    </div>

                    <div v-if="device.condition.active" class="select">
                        <div v-for="condition in device.condition.objects" :key="condition.id" @click="select_condition(device.condition, condition.id)" class="name">{{ condition.name }}</div>
                    </div>

                </div>

                <div class="price">

                    <div class="name">
                        Цена: 
                    </div>

                    <div class="desc">
                        {{ device.price }}₽
                    </div>

                </div>

            </div>

            <div @click="delete_treid_devices_selected(device.id)" class="delete">
                <svg aria-hidden="true" focusable="false" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><path fill="currentColor" d="M193.94 256L296.5 153.44l21.15-21.15c3.12-3.12 3.12-8.19 0-11.31l-22.63-22.63c-3.12-3.12-8.19-3.12-11.31 0L160 222.06 36.29 98.34c-3.12-3.12-8.19-3.12-11.31 0L2.34 120.97c-3.12 3.12-3.12 8.19 0 11.31L126.06 256 2.34 379.71c-3.12 3.12-3.12 8.19 0 11.31l22.63 22.63c3.12 3.12 8.19 3.12 11.31 0L160 289.94 262.56 392.5l21.15 21.15c3.12 3.12 8.19 3.12 11.31 0l22.63-22.63c3.12-3.12 3.12-8.19 0-11.31L193.94 256z"></path></svg>
            </div>

        </div>

        <div v-if="!mode" class="arrows">
            <img src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1633575929/arrow_v822hx.png" />
            <img src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1633575929/arrow_1_mjavcv.png" />
        </div>

        <div v-if="new_devices.selected.length > 0" class="name">
            Вы выбрали:
        </div>

        <div v-for="device in new_devices.selected" :key="device.id" class="device">

            <div class="line"></div>

            <img :src="device.poster" />

            <div class="wrap">

                <div class="name">
                    {{ device.name }}
                </div>

                <div class="price">

                    <div class="name">
                        Цена: 
                    </div>

                    <div class="desc">
                        {{ device.price }}₽
                    </div>

                </div>

            </div>

            <div @click="delete_treid_new_devices_selected(device.id)" class="delete">
                <svg aria-hidden="true" focusable="false" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><path fill="currentColor" d="M193.94 256L296.5 153.44l21.15-21.15c3.12-3.12 3.12-8.19 0-11.31l-22.63-22.63c-3.12-3.12-8.19-3.12-11.31 0L160 222.06 36.29 98.34c-3.12-3.12-8.19-3.12-11.31 0L2.34 120.97c-3.12 3.12-3.12 8.19 0 11.31L126.06 256 2.34 379.71c-3.12 3.12-3.12 8.19 0 11.31l22.63 22.63c3.12 3.12 8.19 3.12 11.31 0L160 289.94 262.56 392.5l21.15 21.15c3.12 3.12 8.19 3.12 11.31 0l22.63-22.63c3.12-3.12 3.12-8.19 0-11.31L193.94 256z"></path></svg>
            </div>

        </div>

        <div class="desc">
            <span>*</span> окончательная стоимость нового устройства будет озвучена после разговора с нашим сотрудником или диагностики в сервисном центре
        </div>

    </div>

</template>

<script>

import { useStore } from 'vuex'
import { computed } from "vue";

export default {

    setup(){
        
        const store     = useStore()
        const mode      = computed(() => store.getters.mode)
        const devices   = computed(() => store.getters.devices)
        const new_devices   = computed(() => store.getters.new_devices)

        function select_condition(condition, id){

            for(let object of condition.objects){
                if(object.id == id){
                    object.active = true
                }else{
                    object.active = false
                }
            }


            condition.active = false
        }

        function get_condition_name(objects){
            for(let object of objects){
                if(object.active){
                    return object.name
                }
            }
        }

        function get_devices_final_price(){
            let temp = 0
            for(let object of devices.value.selected){
                temp += object.price
            }
            return temp
        }

        function get_new_devices_final_price(){
            let temp = 0
            for(let object of new_devices.value.selected){
                temp += object.price
            }
            return temp
        }


        function delete_treid_devices_selected(id){
            store.dispatch("delete_treid_devices_selected", id)
        }

        function delete_treid_new_devices_selected(id){
            store.dispatch("delete_treid_new_devices_selected", id)
        }



        return {

            mode,

            devices,
            select_condition,
            get_condition_name,
            get_devices_final_price,
            delete_treid_devices_selected,

            new_devices,
            delete_treid_new_devices_selected,
            get_new_devices_final_price,


        }
    }
}

</script>