
<template>
	<div class="assoc_objects">
		<div v-for="object in objects" :key="object.id" @click="update(object)" class="object">
            <div class="chekbox">
                <svg v-if="object.def" aria-hidden="true" focusable="false" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path fill="currentColor" d="M435.848 83.466L172.804 346.51l-96.652-96.652c-4.686-4.686-12.284-4.686-16.971 0l-28.284 28.284c-4.686 4.686-4.686 12.284 0 16.971l133.421 133.421c4.686 4.686 12.284 4.686 16.971 0l299.813-299.813c4.686-4.686 4.686-12.284 0-16.971l-28.284-28.284c-4.686-4.686-12.284-4.686-16.97 0z"></path></svg>
            </div>
			<div class="name">{{ object.name }}</div>
		</div>
	</div>
</template>

<script>

import { ref, onMounted, computed } from "vue"
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { get_cookie } from "@/api/cookie";


export default {
	setup(){

        const route = useRoute()
		const store = useStore()
		const domain = computed(() => store.getters.domain)
		const objects = ref([])

		async function get() {

			const response = await fetch(`${domain.value}/api/admin/garantees/subs/${route.params.sd_id}`, {
				method: 'GET',
				headers: {
					"Authorization": 'Bearer ' + get_cookie('token'),
				},
			})

			const body = await response.json()

			return body.objects
		}


        async function update(object) {

            const data = JSON.stringify({
                id: object.id,
                def: !object.def,
            })

			const response = await fetch(`${domain.value}/api/admin/garantees/subs/${route.params.sd_id}/update`, {
				method: 'POST',
				headers: {
                    "Content-Type": "application/x-www-form-urlencoded",
					"Authorization": 'Bearer ' + get_cookie('token'),
				},
                body: `payload=${data}`,
			})

			const body = await response.json()

            if(body.err.is){
                store.dispatch('set_err', body.err)
            }else{
                objects.value = await get()
            }
			
		}

		
		onMounted(async () => objects.value = await get())

		return { objects, update }
	}
}

</script>
