export const delete_subs = async ({ rootState }, id) => {

    await fetch(`${rootState.domain}/api/services/0/subs/${id}/`, {
        method: 'DELETE',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

}

export const get_subs = async ({ commit, rootState }, id) => {

    const response = await fetch(`${rootState.domain}/api/services/${id}/subs/`, {
        method: 'GET',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    const body = await response.json()

    commit('get_subs', body)

}

