<template>

    <div class="wrap-right">

        <pop-up v-if="pop_up"></pop-up>

        <div class="right">
            <div class="name">ОФОРМЛЕНИЕ ЗАЯВКИ</div>
            
            <div class="wrap-input-block">

                <div class="name">ИМЯ<span>*</span></div>

                <div class="input-block">
                    <input v-model="form.name" type="text" placeholder="Как вас зовут?"/>
                </div>

            </div>

            <div class="wrap-input-block">

                <div class="name">ТЕЛЕФОН<span>*</span></div>

                <div class="input-block">
                    <input v-model="form.phone" type="text" placeholder="Ваш номер телефона"/>
                </div>

            </div>

            <div class="wrap-input-block">

                <div class="name">E-MAIL<span>*</span></div>

                <div class="input-block">
                    <input v-model="form.email" type="text" placeholder="Ваша электронная почта"/>
                </div>

            </div>

            <div class="wrap-input-block">

                <div class="name">ВИД ТРАНПОРТИРОВКИ</div>

                <div @click="delivery = !delivery" class="input-block">

                    <div v-for="delivery in active_delivery" :key="delivery.id" class="name">{{ delivery.name }} - {{ delivery.price }} руб.</div>

                    <svg width="14" height="7" viewBox="0 0 14 7" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M1 1L7 6L13 1" stroke="#252525"/>
                    </svg>

                </div>

                <div v-if="delivery" class="wrap-dilivery-block">
                    <div v-for="delivery in object.delivery" :key="delivery.id" @click="set_delivery(delivery)" class="dilivery-block">
                        {{ delivery.name }} - {{ delivery.price }} руб.
                    </div>
                </div>

            </div>

            <div class="form-political">

                <div class="checkbox">
                    <svg viewBox="0 0 10 8" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path d="M9.19533 0.18483L3.17601 6.20415L0.804671 3.83281L0 4.63749L3.17601 7.81349L10 0.989501L9.19533 0.18483Z" fill="currentColor"></path></svg>
                </div>

                <div class="name">
                    Я соглашаюсь с политикой конфиденциальности
                </div>

            </div>

            <div class="warning-name">
                Поля, отмеченные специальным символом * обязательны для заполнения заявки
            </div>

            <div @click="order" class="form-order">Отправить заявку</div>

        </div>
    </div>
</template>

<script>

import { computed, ref } from "vue";
import { useStore } from 'vuex'

import PopUp from '@/components/service/PopUp.vue';

export default {

    components: { 
		"pop-up": PopUp,
    },

    setup(){
        
        const store = useStore()
        const domain = computed(() => store.getters.domain)

        const pop_up = computed(() => store.getters.pop_up_order)
		function pop_up_open(){
			store.dispatch('set_pop_up_order', true)
		}

        const object                        = computed(() => store.getters.sub_service)
        const active_delivery               = computed(() => object.value.delivery.filter((o) => o.active))
		const get_active_simular_service    = computed(() => object.value.simular_service.filter((o) => o.active))
		const get_active_spare_parts        = computed(() => object.value.spare_parts.filter((o) => o.active))


        const form = ref({
            name: "",
			phone: "",
			email: "",
		})

        const delivery = ref(false)

        function set_delivery(local_delivery){

			for(let delivery of object.value.delivery){
				delivery.active = false;
			}
			
			local_delivery.active = true;

			delivery.value = false;
		}

        async function order(){

			//user name
			let user_name = form.value.name;
			if(user_name.length < 1 || user_name.length > 25) return false;

			//user phone
			let user_phone = form.value.phone;
			if(user_phone.length < 1 || user_phone.length > 25) return false;

			//user email
			let user_email = form.value.email;
			if(user_email.length < 1 || user_email.length > 25) return false;

			//service id
			let service_id = object.value.id;

			//guarantee id
			let guarantee_id = 0;
			for(let g of object.value.guarantee){
				if(g.default){
					guarantee_id = g.id; break;
				}
			}

			//masters id
			let master_id = 0;
			for(let master of object.value.masters){
				if(master.default){
					master_id = master.id; break;
				}
			}

			//simular_service ids
			let simular_service_ids = [];
			for(let simular_service of get_active_simular_service.value){
				if(simular_service.active){
					simular_service_ids.push(simular_service.id);
				}
			}

			//simular_service ids
			let spare_parts_ids = [];
			for(let spare_part of get_active_spare_parts.value){
				if(spare_part.active){
					spare_parts_ids.push(spare_part.id);
				}
			}

			//delivery id
			let delivery_id = 0;
			for(let delivery of active_delivery.value){
				if(delivery.active){
					delivery_id = delivery.id;
				}
			}


			let data = new FormData();

			data.append("payload", JSON.stringify({
				"service_id": service_id,
				"guarantee_id": guarantee_id,
				"master_id": master_id,
				"simular_service_ids": simular_service_ids,
				"spare_parts_ids": spare_parts_ids,
				"delivery_id": delivery_id,
				"user_name": user_name,
				"user_phone": user_phone,
				"user_email": user_email,
			}))

            const response = await fetch(`${domain.value}/api/order/detail-service`, {
                method: 'POST',
                body: data,
            });
            const body = await response.json()

            console.log(body)
            pop_up_open()
			
		}

        return {
            pop_up,
            object,
            active_delivery,
            form,
            delivery,
            set_delivery,
            order,

        }
    }
}

</script>