
<template>
    <div class="main admin">

        <div class="name">
           Под. Услуга: {{ sub.name }}
        </div>

        <div class="links">
            <router-link :to="{ name: `admin.subs.info` }" class="link">Основная информация</router-link>
            <router-link :to="{ name: `admin.images` }" class="link">Изображения</router-link>
            <router-link :to="{ name: `admin.sub.services.contents` }" class="link">Контент</router-link>
            <router-link :to="{ name: `admin.subs.garantee.list` }" class="link">Гарантия</router-link>
        </div>

        <div class="tabs">
            <router-view name="sub_service_detail"/>
        </div>

    </div>
</template>

<script>

import { computed, onMounted } from "vue"
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'

export default {
	setup(){

        const store     = useStore()
		const sub       = computed(() => store.getters.sub)
        const route     = useRoute()
        const id        = computed(() => route.params.sd_id)

        onMounted(async () => await store.dispatch('get_sub', route.params.sd_id))
        
		return { id, sub }
	}
}

</script>