
//deliverys

export const update_deliverys = async ({ rootState, dispatch }, object) => {

    const response = await fetch(`${rootState.domain}/api/deliverys/${object.id}/`, {
        method: 'PUT',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': rootState.cookie,
        },
        body: JSON.stringify(object)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const add_deliverys = async ({ rootState, dispatch}, object) => {

    const response = await fetch(`${rootState.domain}/api/deliverys/`, {
        method: 'POST',
        headers: { 
            'Content-Type':'application/json',
            'Authorization': rootState.cookie,
        },
        body: JSON.stringify(object)
    })

    if(!response.ok){

        const body = await response.json()

        await dispatch('update_err_by_code', body.code)

    }

}

export const delete_deliverys = async ({ rootState, dispatch }, id) => {

    await fetch(`${rootState.domain}/api/deliverys/${id}/`, {
        method: 'DELETE',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    await dispatch('get_deliverys')

}

export const get_deliverys = async ({ commit, rootState }) => {

    const response = await fetch(`${rootState.domain}/api/deliverys/`, {
        method: 'GET',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    const body = await response.json()

    commit('get_deliverys', body)

}

export const get_delivery = async ({ commit, rootState }, id) => {

    const response = await fetch(`${rootState.domain}/api/deliverys/${id}`, {
        method: 'GET',
        headers: {
            "Authorization": rootState.cookie,
        },
    })

    const body = await response.json()

    commit('get_delivery', body)

}