
<template>

	<router-link :to="{ name: `admin.subs.add` }" class="link">Добавить</router-link>

	<div class="objects">

		<div v-for="object in subs" :key="object.id" class="object">
			<router-link :to="{ name: `admin.subs.info`, params: { sd_id: object.id } }" class="name">{{ object.name }}</router-link>
			<svg @click="del(object.id)" aria-hidden="true" focusable="false" data-prefix="fal" data-icon="times" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><path fill="currentColor" d="M193.94 256L296.5 153.44l21.15-21.15c3.12-3.12 3.12-8.19 0-11.31l-22.63-22.63c-3.12-3.12-8.19-3.12-11.31 0L160 222.06 36.29 98.34c-3.12-3.12-8.19-3.12-11.31 0L2.34 120.97c-3.12 3.12-3.12 8.19 0 11.31L126.06 256 2.34 379.71c-3.12 3.12-3.12 8.19 0 11.31l22.63 22.63c3.12 3.12 8.19 3.12 11.31 0L160 289.94 262.56 392.5l21.15 21.15c3.12 3.12 8.19 3.12 11.31 0l22.63-22.63c3.12-3.12 3.12-8.19 0-11.31L193.94 256z"></path></svg>
		</div>

	</div>

</template>

<script>

import { onMounted, computed } from "vue"
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

export default {
	setup(){

		const route	= useRoute()
		const store = useStore()
		const subs	= computed(() => store.getters.subs)

		

		onMounted(async () => {
			
			await store.dispatch('get_subs', route.params.id)

		})
        
        async function del(id){
            await store.dispatch('delete_subs', id)
			await store.dispatch('get_subs', route.params.id)
        }


		return { subs, del }
	}
}

</script>
