<template>
	<div class="main service">

		<div class="form">
			
			<h2>Войти</h2>

			<div class="input">
				<input v-model="login" type="text" placeholder="Логин" />
			</div>

			<div class="input">
				<input v-model="passw" type="password" placeholder="Пароль" />
			</div>

			<div @click="auth" class="btn">войти</div>

		</div>

	</div>
</template>

<script>

import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

import { set_cookie } from "@/api/cookie";


export default {

	setup(){

		const router = useRouter()
		const store = useStore()
		const domain = computed(() => store.getters.domain)
		const user = computed(() => store.getters.user)

		const login = ref('')
		const passw = ref('')

		async function auth() {

			const response = await fetch(`${domain.value}/auth/jwt/create`, {
				method: 'POST',
				headers: {
					"Content-Type": "application/x-www-form-urlencoded",
				},
				body: `username=${login.value}&password=${passw.value}`,
			})


			if(!response.ok){
				store.dispatch('update_err_by_code', 142); return
			}

			const body = await response.json()

			//устанавливаем куки в браузер
			set_cookie("token", body.access)

			//устанавливаем куки в хранилище
			store.dispatch('update_cookie', `Bearer ${body.access}`)

			//обновляем юзера
			await store.dispatch('get_user')

			//переходим в кабинет
			router.push({ name: `admin` })
				
		}

		onMounted( () => {
			if(user.value){
				router.push({ name: `admin` })
			}
		})

		return {
			domain,
			auth,
			login,
			passw,
		}
    
	}

}

</script>