
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Добавление под-услуги</div>
            </div>

            <div class="cnt">

                <div class="wrap input">
                    <label for="input-180">Наименование</label>
                    <input v-model="sub.name" id="input-180" />
                </div>

                <div class="wrap textarea">
                    <label for="input-179">Описание</label>
                    <textarea id="input-179" v-model="sub.pre_content" rows="5"></textarea>
                </div>

                <div class="wrap input">
                    <label for="input-180">Короткий адрес</label>
                    <input v-model="sub.slug" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">Сео заголовок страницы</label>
                    <input v-model="sub.seo_t" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">Сео описание страницы</label>
                    <input v-model="sub.seo_d" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">Цена</label>
                    <input v-model="sub.price" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">Скидка</label>
                    <input v-model="sub.discount" id="input-180" />
                </div>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="sub.price_from" id="input-180" />
                    <label for="input-180">Цена от?</label>
                </div>

                <div class="wrap input">
                    <label for="input-180">Время исполнения</label>
                    <input v-model="sub.execution_time" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">Что с собой взять</label>
                    <input v-model="sub.take" id="input-180" />
                </div>

            </div>

            <div class="btm">
                <div @click="add()" :class="['send', 'active']">Добавить</div>
                <router-link :to="{ name: `admin.subs.list`, }" class="cancel">Отмена</router-link>
            </div>

        </div>

    </div>
</template>

<script>


import { computed } from "vue"
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { useRoute } from 'vue-router'

export default {
	setup(){

        const router    = useRouter()
        const route     = useRoute()
		const store     = useStore()
        const sub       = computed(() => store.getters.sub)

        async function add() {

            sub.value.parent = route.params.id

			await store.dispatch('add_subs', sub.value)

            router.push({ name: `admin.subs.list` })
			
		}

		return { add, sub }
	}
}

</script>