
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Изменить фотку</div>
            </div>

            <div class="cnt">
                
                <div class="name">
                    Текущее изображение    
                </div>

                <a v-if="image.image" :href="`${domain + image.image}_1500.webp`" target="_blank" class="img">
                    <img :src="`${domain + image.image}_750.webp`" />
                </a>

                <form-image></form-image>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="image.default" id="input-180" />
                    <label for="input-180">Дефолтная фотка?</label>
                </div>

            </div>

            <div class="btm">
                <div @click="update" :class="['send', 'active']">Изменить</div>
                <div @click="back" class="cancel">Отмена</div>
            </div>


        </div>

    </div>

</template>

<script>

import { computed, onMounted } from "vue"
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
import { useRouter } from 'vue-router'

import Image from '@/components/form/Image.vue';

export default {

    components: { 
        "form-image": Image,
    },

	setup(){

        const router    = useRouter()
        const route     = useRoute()
		const store     = useStore()
        const domain    = computed(() => store.getters.domain)
        const url       = computed(() => store.getters.url)

        const sub       = computed(() => store.getters.sub)
        const images    = computed(() => store.getters.images)
        const image     = computed(() => store.getters.image)

		async function back(){

            if(url.value != ''){
                await store.dispatch('delete_image_server', url.value)
            }

            router.push({ name: `admin.images` })

        }

        async function update(){

            if(url.value != ''){
                
                await store.dispatch('delete_image_server', image.value.image)
            
                image.value.image = url
            }


            for(let temp of images.value){

                if(temp.default){
                    if(temp.id != image.value.id){

                        temp.default = false

                        await store.dispatch('update_images', temp)

                        break

                    }
                }

            }
  
            

            await store.dispatch('update_images', image.value)

            if(image.value.default){

                sub.value.poster = image.value.image

                await store.dispatch('update_subs', sub.value)

            }

            await store.dispatch('update_url', '')

            await back()

        }

        onMounted(async () => await store.dispatch('get_image', route.params.i_id))


		return { domain, image, update, back }
	}
}

</script>