<template>

    <div class="form">

        <div class="name">Оставьте заявку на ремонт!</div>
        <div class="desc">Оставьте заявку и получите <span>скидку на заказ 10%</span></div>

        <div class="block">
            <div class="name">Имя<span>*</span></div>
            <div class="wrap">
                <input v-model="name" type="text" placeholder="Как вас зовут?">
            </div>
        </div>
        
        <div class="block">
            <div class="name">Телефон<span>*</span></div>
            <div class="wrap">
                <input v-model="phone" type="text" placeholder="Ваш номер телефона"/>
            </div>
        </div>

        <div class="block">
            <div class="name">e-mail</div>
            <div class="wrap">
                <input v-model="email" type="text" placeholder="Ваша электронная почта">
            </div>
        </div>

        <div class="block">
            <div class="name">Проблема</div>
            <div class="wrap">
                <input v-model="comment" type="text" placeholder="В двух словах опишите вашу проблему">
            </div>
        </div>

        <div class="warning">Заполняя форму, вы автоматически соглашае&shy;тесь с политикой конфиденциаль&shy;ности. Поля, отмеченные специальным символом <span>*</span> обя&shy;за&shy;тельны для заполнения заявки.</div>

        <div @click="order" class="button">Отправить заявку</div>

        <div class="label">
            <img src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1595606422/icon_1_inqyvr.png">
        </div>

        <pop-up v-if="pop_up"></pop-up>

    </div>

    

</template>

<script>
import { ref, computed } from "vue";
import { useStore } from 'vuex'

import PopUp from '@/components/service/PopUp.vue';

export default {
    props: {
        id: Number,
    },

    components: { 
		"pop-up": PopUp,
    },

	setup(props){
		const store = useStore()
		const domain = computed(() => store.getters.domain)

		const name = ref('')
		const phone = ref('')
        const email = ref('')
        const comment = ref('')

        const pop_up = computed(() => store.getters.pop_up_order)
		function pop_up_open(){
			store.dispatch('set_pop_up_order', true)
		}

        async function order(){

			//user name
			if(name.value.length < 1 || name.value.length > 25) return false;

			//user phone
			if(phone.value.length < 1 || phone.value.length > 25) return false;

			//user email
			if(email.value.length < 1 || email.value.length > 25) return false;
            
			let data = new FormData();

			data.append("service_id", props.id)
            data.append("user_name", name.value)
            data.append("user_phone", phone.value)
            data.append("user_email", email.value)
            data.append("comment", comment.value)

            const response = await fetch(`${domain.value}/api/order/service`, {
                method: 'POST',
                body: data,
            });
            const body = await response.json()

            if(!body.err.is){

                name.value = ""
                phone.value = ""
                email.value = ""
                comment.value = ""

                pop_up_open()

            }
			
		}

		return {

            pop_up,

			name,
			phone,
            email,
            comment,
            order,
		}

	}

}

</script>