
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name">Добавить блок контента</div>
            </div>

            <div class="cnt">
                
                <div class="name">
                    Текущее изображение    
                </div>

                <a :href="`${domain + content.image}_1500.webp`" target="_blank" class="img">
                    <img :src="`${domain + content.image}_750.webp`" />
                </a>

                <form-image></form-image>

                <div class="wrap textarea">
                    <label for="input-179">Контент</label>
                    <textarea id="input-179" v-model="content.content" placeholder="Опишите блок" rows="5"></textarea>
                </div>

                <!--
                    <div class="desc">Вы можете указать контакт для связи, если хотите, чтобы мы связались с вами для обсуждения.</div>
                -->

                <div class="wrap input">
                    <label for="input-180">Сортировка</label>
                    <input v-model="content.order" id="input-180" placeholder="Как будут сортироваться блоки" />
                </div>

            </div>

            <div class="btm">
                <div @click="update()" :class="['send', {active: content.content.length > 0}]">Изменить</div>
            </div>


        </div>

    </div>

</template>

<script>

import { computed, onMounted, onUnmounted } from "vue"
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

import Image from '@/components/form/Image.vue';

export default {

    components: { 
        "form-image": Image,
    },

	setup(){


        const route         = useRoute()
		const store         = useStore()
        const domain        = computed(() => store.getters.domain)
        const url           = computed(() => store.getters.url)
        const content       = computed(() => store.getters.content)


        onMounted(async () => {
            await store.dispatch('get_service_content', route.params.cid)
        })

		onUnmounted(async () => {
            if(url.value != ''){
                await store.dispatch('delete_image_server', url.value)
            }
        })

        async function update(){

            if(url.value != ''){
                
                await store.dispatch('delete_image_server', content.value.image)
            
                content.value.image = url
            }

            await store.dispatch('update_contents')

            await store.dispatch('update_url', '')

            await store.dispatch('get_service_content', route.params.cid)

        }

		return { domain, content, update }
	}
}

</script>