
<template>

    <div class="name">
        - {{ service.name }}
    </div>

    <div class="links">
        
        <router-link :to="{ name: `admin.services.info` }" class="link">Основная информация</router-link>
        <router-link :to="{ name: `admin.services.contents` }" class="link">Контент</router-link>
        <router-link :to="{ name: `admin.subs.list` }" class="link">Под-услуги</router-link>

    </div>

    <router-view name="service_detail"/>

</template>

<script>

import { computed, onMounted } from "vue"
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'

export default {

	setup(){

        const store     = useStore()
		const service   = computed(() => store.getters.service)
        const route     = useRoute()
        const id        = computed(() => route.params.id)


        onMounted(async () => await store.dispatch('get_service', route.params.id))

		return { id, service }
	}
}

</script>