<template>
    <div class="mobile">

        <div v-if="menu.count > 0" @click="back" class="back">
            <svg viewBox="0 0 7 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M6 1L0.999999 6.5L6 12" stroke="#FAFAFA"/>
            </svg>

			<div class="name">Назад</div>
		</div>

		<div v-for="obj in menu_filter" :key="obj.id" :class="['menu', {hover: obj.hover}]" @mouseover="obj.hover = true" @mouseleave="obj.hover = false" @click="next(obj.id)">

            <div v-show="obj.hover" class="line"></div>

            <div class="icon" v-if="obj.icon" v-html="obj.icon"></div>

            <div class="name">{{ obj.name }}</div>

            <svg v-show="obj.hover" viewBox="0 0 7 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M1 12L6 6.5L1 1" stroke="#98C1FF"/>
            </svg>

		</div>


        <img class="decoration-1" src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1614484205/Ellipse_696_h1xu2f.png">
        <img class="decoration-2" src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1614484208/Rectangle_523_wnhtoe.png">
        <img class="decoration-3" src="https://res.cloudinary.com/shop-consoles-ru/image/upload/v1614484206/Rectangle_522_ihj6ww.png">



    </div>
</template>

<script>

import { computed, onMounted } from 'vue'
import { useStore } from 'vuex'

export default {

    setup(){

        const store = useStore()
        const domain = computed(() => store.getters.domain)

        const menu = computed(() => store.getters.menu)

        onMounted(() => store.dispatch('update_menu'))

        const menu_filter = computed(() => menu.value.objects.filter( (o) => o.active ))

        function next(id){
            store.dispatch('next_menu', id)
        }

        function back(){
            store.dispatch('back_menu')
        }


        return { domain, menu, menu_filter, next, back }
    
    },

}

</script>